/**
* FONT SIZES
 */
body {
  font-family: "Karla", sans-serif;
  color: #262626;
  font-size: 16px;
  line-height: 19px; }
  @media (min-width: 1072px) {
    body {
      font-size: 18px;
      line-height: 24px; } }

h1,
.page .entry-title,
.page-title,
body.page:not(.twentyseventeen-front-page) .entry-title {
  font-size: 30px;
  line-height: 36px;
  text-transform: none;
  font-weight: bold;
  letter-spacing: 0; }
  @media (min-width: 1072px) {
    h1,
    .page .entry-title,
    .page-title,
    body.page:not(.twentyseventeen-front-page) .entry-title {
      font-size: 42px;
      line-height: 48px; } }

h2 {
  font-size: 27px;
  line-height: 31px;
  text-transform: none;
  color: #0087FF;
  font-weight: bold; }
  @media (min-width: 1072px) {
    h2 {
      font-size: 36px;
      line-height: 42px; } }

h3 {
  font-size: 18px;
  line-height: 22px;
  text-transform: none;
  font-weight: bold; }
  @media (min-width: 1072px) {
    h3 {
      font-size: 24px;
      line-height: 30px; } }

h4 {
  font-size: 14px;
  line-height: 20px;
  text-transform: none;
  font-weight: bold; }
  @media (min-width: 1072px) {
    h4 {
      font-size: 16px;
      line-height: 19px; } }

h5 {
  font-size: 14px;
  line-height: 20px;
  text-transform: none;
  font-weight: bold; }

.navigation-top {
  background-color: #262626; }
  .navigation-top .current-menu-item > a,
  .navigation-top .current_page_item > a {
    color: #84FAB0; }

.main-navigation .menu-item {
  font-size: 16px;
  line-height: 19px;
  border: 0; }
  .main-navigation .menu-item a {
    color: white;
    padding-left: 0.75em;
    padding-right: 0.75em; }
  .main-navigation .menu-item a:hover {
    color: #84FAB0; }

.main-navigation .sub-menu {
  background-color: #0087FF; }
  .main-navigation .sub-menu li:hover {
    background-color: #0087FF; }
  .main-navigation .sub-menu .menu-item a:hover {
    color: white;
    padding-left: 40px; }

/* MOBILE MENU STUFF */
.menu-toggle {
  color: #84FAB0; }

.dropdown-toggle {
  color: white; }

.menu-main-menu-container ul {
  background-color: #262626; }

.menu-main-menu-container .sub-menu {
  background-color: #262626; }
  .menu-main-menu-container .sub-menu li:hover {
    background-color: #262626; }

.site-footer {
  background-color: #262626;
  color: white;
  font-size: 16px;
  line-height: 19px; }
  @media (min-width: 1072px) {
    .site-footer {
      font-size: 18px;
      line-height: 24px; } }
  .site-footer h2.widget-title {
    color: #84FAB0;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 0;
    text-transform: none; }
    @media (min-width: 1072px) {
      .site-footer h2.widget-title {
        font-size: 18px;
        line-height: 24px; } }

#secondary {
  font-size: 16px;
  line-height: 19px; }
  @media (min-width: 1072px) {
    #secondary {
      font-size: 18px;
      line-height: 24px; } }

.widget {
  background-color: #262626;
  color: white;
  padding: 20px;
  margin-bottom: 40px;
  border-radius: 10px; }

h2.widget-title {
  color: white;
  letter-spacing: 0;
  text-transform: none;
  font-size: 16px;
  line-height: 19px; }
  @media (min-width: 1072px) {
    h2.widget-title {
      font-size: 18px;
      line-height: 24px; } }

